






















export default {
	data() {
		return {
			imageList:[],
			data:{src:'http://qiniu.satapp.cn/fuzhi/swiper.png',detail:'桦木经典美式复古家居'}
		};
	},
	onLoad() {
		this.imageList=this.randomfn()
	},
	methods: {
		randomfn() {
			let arr=[];
			for (var i = 0; i < 10; i++) {
				arr.push(this['data'])
			}
			return arr;
		}
	},
}
